.topbar {
  position: relative;
  z-index: 9;
  height: 70px;
  align-items: center;
  display: flex;
  justify-content: end;
}
.topbar #vibeji-ham {
  width: 25px;
  height: 20px;
  position: absolute;
  left: 15px;
  top: 20px;
  transform: rotate(0);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  display: none;
  margin-right: 0 !important;
}
.topbar #vibeji-ham span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #656867;
  border-radius: 9px;
  opacity: 1;
  right: 0;
  transform: rotate(0);
  transition: 0.25s ease-in-out;
}
.topbar #vibeji-ham span:nth-child(1) {
  top: 0;
  width: 100%;
}
.topbar #vibeji-ham span:nth-child(2) {
  top: 7px;
  width: 100%;
}
.topbar #vibeji-ham span:nth-child(3) {
  top: 14px;
  width: 100%;
}
.topbar #vibeji-ham.open span:nth-child(1) {
  top: 9px;
  width: 100%;
  transform: rotate(135deg);
}
.topbar #vibeji-ham.open span:nth-child(2) {
  opacity: 0;
  right: -30px;
  width: 100%;
}
.topbar #vibeji-ham.open span:nth-child(3) {
  top: 9px;
  width: 100%;
  transform: rotate(-135deg);
}
.topbar .logo {
  display: none;
  margin-bottom: 0;
  font: 600 16px/1.5 var(--font-main);
  color: var(--colormain);
  margin-left: 40px;
}
.topbar .logo a {
  display: flex;
  gap: 5px;
  align-items: center;
}
.topbar .logo img {
  width: 40px;
  flex-shrink: 0;
}
.topbar .pagination-lesson {
  display: none;
  align-items: center;
  column-gap: 10px;
}
.topbar .pagination-lesson .btn-lesson,
.topbar .pagination-lesson .btn-bxh {
  display: flex;
  column-gap: 2px;
  height: 36px;
  min-width: 45px;
  padding: 0 8px;
  align-items: center;
  justify-content: center;
  background: #F1F8F5;
  border: 1px solid #DBE2DF;
  border-radius: 4px;
  font-size: 12px;
  color: #656867;
}
.topbar .pagination-lesson .btn-lesson svg,
.topbar .pagination-lesson .btn-bxh svg {
  width: 12px;
  height: 12px;
}
.topbar .pagination-lesson .btn-bxh {
  background: #F4A100;
  border: 1px solid #F4A100;
  color: #034C31;
}
.topbar .pagination-lesson .btn-bxh svg {
  width: 20px;
  height: 20px;
}
.topbar .pagination-lesson .open-comment {
  background: linear-gradient(279deg, #A0D9BA 0%, #2E8B57 100%);
  color: #fff;
  padding: 0 6px;
  font-size: 13px;
}
.topbar .pagination-lesson .open-comment span {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M15 5.0625C16.1391 5.0625 17.0625 4.13909 17.0625 3C17.0625 1.86091 16.1391 0.9375 15 0.9375C13.8609 0.9375 12.9375 1.86091 12.9375 3C12.9375 4.13909 13.8609 5.0625 15 5.0625Z' fill='%23FF5E57'/%3e%3cpath d='M14.28 6.1125C13.1025 5.8575 12.1425 4.8975 11.8875 3.72C11.79 3.2625 11.7825 2.82 11.865 2.4C11.9625 1.935 11.6175 1.5 11.1375 1.5H5.25C3.18 1.5 1.5 3.18 1.5 5.25V10.4625C1.5 12.5325 3.18 14.2125 5.25 14.2125H6.375C6.585 14.2125 6.855 14.3475 6.975 14.5125L8.1 16.005C8.595 16.665 9.405 16.665 9.9 16.005L11.025 14.5125C11.1675 14.325 11.385 14.2125 11.625 14.2125H12.7575C14.8275 14.2125 16.5 12.54 16.5 10.47V6.8625C16.5 6.39 16.065 6.045 15.6 6.135C15.18 6.21 14.7375 6.21 14.28 6.1125ZM6 9C5.58 9 5.25 8.6625 5.25 8.25C5.25 7.8375 5.58 7.5 6 7.5C6.4125 7.5 6.75 7.8375 6.75 8.25C6.75 8.6625 6.42 9 6 9ZM9 9C8.58 9 8.25 8.6625 8.25 8.25C8.25 7.8375 8.58 7.5 9 7.5C9.4125 7.5 9.75 7.8375 9.75 8.25C9.75 8.6625 9.42 9 9 9ZM12 9C11.58 9 11.25 8.6625 11.25 8.25C11.25 7.8375 11.58 7.5 12 7.5C12.4125 7.5 12.75 7.8375 12.75 8.25C12.75 8.6625 12.42 9 12 9Z' fill='url(%23paint0_linear_7636_7594)'/%3e%3cdefs%3e%3clinearGradient id='paint0_linear_7636_7594' x1='16.5' y1='16.5' x2='-0.465679' y2='13.7855' gradientUnits='userSpaceOnUse'%3e%3cstop stop-color='white'/%3e%3cstop offset='1' stop-color='%23E3F1EC'/%3e%3c/linearGradient%3e%3c/defs%3e%3c/svg%3e");
  background-position: right;
}
.topbar .my-notifi {
  color: #fff;
  position: relative;
}
.topbar .my-notifi::before {
  content: "";
  position: absolute;
  right: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #ff5e57;
}
.topbar .my-user {
  display: flex;
  align-items: center;
  column-gap: 20px;
  row-gap: 20px;
}
.topbar .my-login {
  position: relative;
}
.topbar .my-login .ava img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.topbar .my-login .menu-user {
  position: absolute;
  min-width: 220px;
  background: #fff;
  right: -20px;
  top: 46px;
  border-radius: 8px;
  overflow: hidden;
}
.topbar .my-login .menu-user li {
  font-size: 16px;
  transition: 0.2s all;
}
.topbar .my-login .menu-user li a {
  display: flex;
  color: #4E565C;
  align-items: center;
  column-gap: 8px;
  row-gap: 8px;
  padding: 8px 16px;
}
.topbar .my-login .menu-user li .icon-svg {
  fill: #4E565C;
}
.topbar .my-login .menu-user li.active, .topbar .my-login .menu-user li:hover {
  background: #68BBA0;
}
.topbar .my-login .menu-user li.active a, .topbar .my-login .menu-user li:hover a {
  color: #fff;
}
.topbar.space-between {
  justify-content: space-between;
}
.topbar.space-between .breadcrumb-course {
  border-bottom: 0;
  height: 40px;
}
.topbar.space-between .my-notifi {
  color: #656867;
}
@media (max-width: 991px) {
  .topbar .my-user {
    column-gap: 12px;
    row-gap: 12px;
  }
  .topbar.detail .pagination-lesson {
    display: flex;
  }
  .topbar.detail .my-user {
    display: none;
  }
}
@media (max-width: 600px) {
  .topbar {
    height: 60px;
    background: #ffffff;
    border-bottom: 1px solid #DBE2DF;
    justify-content: space-between;
  }
  .topbar .breadcrumb-course {
    display: none;
  }
  .topbar #vibeji-ham {
    display: block;
  }
  .topbar .logo {
    display: block;
  }
  .topbar .my-notifi {
    color: #333;
  }
  .topbar .my-user .btn-default {
    display: none;
  }
  .topbar .my-login .ava img {
    border: 0;
  }
  .topbar.detail .logo span {
    display: none;
  }
  .topbar.sticky {
    position: sticky;
  }
}

.menu-mobile {
  position: sticky;
  top: 0;
  z-index: 8;
  display: none;
  height: 60px;
  align-items: center;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
  background: #fff;
  white-space: nowrap;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
.menu-mobile::-webkit-scrollbar {
  display: none;
}
.menu-mobile a {
  font-weight: 500;
  color: #012B1C;
  height: 36px;
  display: flex;
  gap: 6px;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
}
.menu-mobile .home {
  width: 40px;
  height: 40px;
  padding: 0;
  flex-shrink: 0;
  border-radius: 6px;
  background: #F1F8F5;
  margin-right: 12px;
}
@media (max-width: 600px) {
  .menu-mobile {
    display: flex;
  }
}

.main-left {
  width: 340px;
  flex-shrink: 0;
  padding-left: 30px;
}
.main-left .side-panel {
  position: sticky;
  top: 0;
  height: 100dvh;
  width: 100%;
}
.main-left .header {
  padding: 10px 0;
  position: relative;
}
.main-left .header .close-menu {
  position: absolute;
  right: 15px;
  top: 20px;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  display: none;
  justify-content: center;
  align-items: center;
}
.main-left .header .close-menu svg {
  width: 30px;
  height: 30px;
}
.main-left .logo {
  display: flex;
  gap: 12px;
  align-items: center;
  font: 600 18px/1.5 var(--font-main);
  color: var(--colormain);
}
.main-left .logo img {
  width: 60px;
  flex-shrink: 0;
}
.main-left .side-panel-scroll {
  height: calc(100dvh - 90px);
  padding-right: 30px;
  padding-bottom: 20px;
  overflow-y: auto;
  overflow-x: hidden;
}
.main-left .side-panel-scroll::-webkit-scrollbar {
  width: 5px;
  background: transparent;
}
.main-left .side-panel-scroll::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.3s ease;
}
.main-left .side-panel-scroll::-webkit-scrollbar-track {
  background: #f9f9f9;
  border-radius: 5px;
}
.main-left .side-panel-scroll:hover::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: rgba(0, 0, 0, 0.2);
}
.main-left .side-panel-scroll ul {
  border-bottom: 1px solid #DBE2DF;
}
.main-left .side-panel-scroll ul li {
  margin-bottom: 8px;
  padding: 10px 0;
  border-radius: 8px;
  transition: 0.5s;
  position: relative;
}
.main-left .side-panel-scroll ul li a {
  font-weight: 500;
  color: #012B1C;
  display: flex;
  align-items: center;
  gap: 12px;
  padding-left: 20px;
  position: relative;
}
.main-left .side-panel-scroll ul li a .ic {
  width: 36px;
  flex-shrink: 0;
}
.main-left .side-panel-scroll ul li .number {
  position: absolute;
  right: 0;
  top: 10px;
  font-size: 12px;
  font-weight: 500;
  width: 36px;
  text-align: center;
  border: 1px solid var(--colormain);
  border-radius: 50%;
  line-height: 28px;
}
.main-left .side-panel-scroll ul li.active, .main-left .side-panel-scroll ul li:hover {
  background: linear-gradient(276deg, rgba(255, 255, 255, 0) 0%, #E3F1EC 90.88%);
  transform: translateY(-4px);
}
.main-left .side-panel-scroll ul li.active a::before, .main-left .side-panel-scroll ul li:hover a::before {
  content: "";
  position: absolute;
  background: var(--colormain);
  width: 6px;
  height: 100%;
  left: 0;
}
@media (max-width: 1480px) {
  .main-left .side-panel-scroll {
    padding-right: 20px;
  }
}
@media (max-width: 600px) {
  .main-left .side-panel-scroll {
    padding-right: 15px;
  }
}
@media (max-width: 1480px) {
  .main-left {
    width: 310px;
    padding-left: 20px;
  }
}
@media only screen and (max-width: 1025px) and (min-width: 601px) {
  .main-left {
    width: 80px;
    padding: 0 16px 16px 16px;
  }
  .main-left .header {
    padding: 16px 0 0 0;
  }
  .main-left .logo {
    margin-bottom: 30px;
  }
  .main-left .logo span {
    display: none;
  }
  .main-left .side-panel-scroll {
    padding-right: 0;
    padding-bottom: 30px;
  }
  .main-left .side-panel-scroll ul {
    margin-bottom: 10px;
  }
  .main-left .side-panel-scroll ul li {
    padding: 0;
    margin-bottom: 5px;
  }
  .main-left .side-panel-scroll ul li a {
    padding: 0;
    border-radius: 8px;
    width: 48px;
    height: 48px;
    justify-content: center;
    align-items: center;
    font-size: 0;
    gap: 0;
  }
  .main-left .side-panel-scroll ul li a::before {
    display: none;
  }
  .main-left .side-panel-scroll ul li .number {
    display: none;
  }
  .main-left .side-panel-scroll ul li.active a {
    background: #FEFEFE;
  }
  .main-left .side-panel-scroll ul:last-of-type {
    margin-bottom: 0;
    border-bottom: 0;
  }
  .main-left .side-panel-scroll .footer {
    display: none;
  }
}
@media (max-width: 600px) {
  .main-left {
    background: #f1f8f5;
    position: fixed;
    top: 0;
    bottom: 0;
    left: -100%;
    width: 100% !important;
    -webkit-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    z-index: 1004;
    padding-left: 15px;
  }
  .main-left.open {
    left: 0;
  }
  .main-left .header {
    margin-bottom: 15px;
    margin-left: -15px;
    padding: 6px 15px;
  }
  .main-left .header .close-menu {
    display: flex;
  }
  .main-left .logo {
    margin-bottom: 0;
  }
  .main-left .side-panel-scroll .footer .wrap {
    border-radius: 0;
    background: #4bfabf url(images/graphics/m_bg-footer.png) no-repeat center bottom;
    padding: 12px 10px 130px 10px;
    background-size: contain;
  }
  .main-left .side-panel-scroll .footer .grid__4 {
    grid-template-columns: repeat(4, 1fr);
  }
}

