  background: #F1F8F5;
  border-bottom: 1px solid #DBE2DF;
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
.breadcrumb-course .nav {
  display: flex;
  align-items: center;
  gap: 36px;
}
