.breadcrumb-course .pagination-lesson a svg {
  width: 14px;
  height: 14px;
}
.breadcrumb-course .pagination-lesson a:hover {
  color: #2d8755;
}
.breadcrumb-course .pagination-lesson a::after {
  display: none;
}
@media (max-width: 991px) {
  .breadcrumb-course .pagination-lesson {
    display: none;
  }
}
@media (max-width: 600px) {
  .breadcrumb-course {
